import Link from 'next/link'
import { connect } from 'react-redux'

import Counter from './counter'
import Clock from './clock'

function Page ({
  error,
  lastUpdate,
  light,
  linkTo,
  NavigateTo,
  placeholderData,
  title
}) {
  return (
    <div>
      <h1>{title}</h1>
      <Clock lastUpdate={lastUpdate} light={light} />
      <Counter />
      <nav>
        <Link href={linkTo}>
          <a>Navigate: {NavigateTo}</a>
        </Link>
      </nav>
      <ul>
          {placeholderData && (
              placeholderData.map((item,index)=>{
                  return(
                      <li key={item.id}>
                          <h3>{item.title}</h3>
                          <div>
                              <div>
                                  <img src={'https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1545231176&di=852f54ec8e7fe85b1db442d57118682c&src=http://pic.51yuansu.com/pic3/cover/01/69/80/595f67c2239cb_610.jpg'}/>
                              </div>
                              <div></div>
                          </div>
                      </li>
                  )
              })
          )}
      </ul>
      {error && <p style={{ color: 'red' }}>Error: {error.message}</p>}
    </div>
  )
}

export default connect(state => state)(Page)
